<template>
	<el-card class="box-card">
		<div slot="header" class="clearfix">
			<span>UrlEncode编码/UrlDecode解码</span>
		</div>
		<el-input type="textarea" v-model="decoContent" placeholder="转换的内容黏贴在这里"></el-input>

		<div class="htl-deco-btg">
			<el-button-group>
				<el-button type="primary" v-on:click="un_escape">编码/解码</el-button>
			</el-button-group>
		</div>
	</el-card>
</template>

<script>
	export default {
		name: 'Deco',
		data() {
			return {
				decoContent: ''
			}
		},
		methods: {
			un_escape: function () {
				if (this.decoContent == unescape(this.decoContent)) {
					this.decoContent = escape(this.decoContent);
				} else {
					this.decoContent = unescape(this.decoContent);

				}
			}
		}
	}
</script>

<style>
	.htl-deco {
		width: 100%;
	}

	.htl-deco-title {
		text-align: left;
	}

	.el-textarea {
		wdith: 100%;
		height: 200px;
	}

	.el-textarea>textarea {
		width: 100%;
		height: 100%;
		resize: none;
	}

	.htl-deco-btg {
		margin-top: 10px;
		padding: 0px;
		text-align: right;
	}
</style>



