<template>
	<div>
		<NvwasLogo></NvwasLogo>
		<div class="htl-deco-page">
			<div style="padding: 20px;">
				<el-breadcrumb separator="/">
					<el-breadcrumb-item><a href="http://guide.nvwas.com">首页</a></el-breadcrumb-item>
					<el-breadcrumb-item>编码·解码</el-breadcrumb-item>
				</el-breadcrumb>
			</div>
			<Deco/>
		</div>
	</div>
</template>

<script>
	import Deco from '@/components/Deco'
	import NvwasLogo from '@/components/NvwasLogo'

	export default {
		components: {
			Deco,
			NvwasLogo
		},
		name: 'DecoPage',
		data() {
			return {}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	html {
		background: url("data:image/jpeg;base64,");
	}

	.logo {
		height: 40px;
		background: red;
		margin: 0px;
	}

	.htl-deco-page {
		width: 960px;
		margin: 0px auto;
	}
</style>



