import Vue from 'vue'
import Router from 'vue-router'
import DecoPage from '@/page/DecoPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DecoPage',
      component: DecoPage
    },
		{
			path: '/DecoPage',
			name: 'DecoPage',
			component: DecoPage
		}
  ]
})



