<template>
	<div class="nvw-header">
		<div class="nvw-header-container">
			<img v-bind:src="logo" />
		</div>
	</div>
</template>

<script>
	export default {

		name: 'NvwasLogo',
		data() {
			return {
				logo: "data:image/png;base64,"
			}
		}
	}
</script>

<style>
	.nvw-header {
		width: 100%;
		background-color: #409EFF;
	}
	.nvw-header-container{
		width: 960px;
		text-align: left;
		margin: 0px auto;
		height: 60px;
		line-height: 60px;
	}
	.nvw-header-container >img{
		height: 40px;
		 vertical-align: middle;
	}
</style>



